import { _decorator, Component, Node, director } from 'cc';
const { ccclass, property } = _decorator;

import { AudioManager } from "../common/AudioManager";

@ccclass('PrefabTools')
export class PrefabTools extends Component {
    start() {

    }

    update(deltaTime: number) {
        
    }



    clickLove(){
        AudioManager.playSoundClick();
      if(window["typeTool"] == 1) {
        return;
      }
      director.loadScene("Love");

    }

    clickPhone(){
        AudioManager.playSoundClick();
      if(window["typeTool"] == 2) {
        return;
      }
      director.loadScene("Phone");

    }

    clickAddress(){
        AudioManager.playSoundClick();
      if(window["typeTool"] == 3) {
        return;
      }
      director.loadScene("Address");

    }

}

